.hero-sec {
  background: linear-gradient(hsl(252, 100%, 67%), hsl(241, 81%, 54%));
  display: flex;
  flex-direction: column;
}
.hero-sec h1 {
  color: hsl(241, 100%, 89%);
}
.hero-sec h2 {
  color: hsl(0, 0%, 100%);
  background: linear-gradient(hsl(256, 72%, 46%), hsla(241, 72%, 46%, 0));
  min-width: 7.4138888889rem;
  min-height: 7.1444444444rem;
  align-self: center;
  border-radius: 50%;
  padding: 1.5rem;
}
.hero-sec h2 span {
  color: hsla(241, 100%, 89%, 0.5);
}
.hero-sec h3 {
  color: hsl(0, 0%, 100%);
}
.hero-sec p {
  color: hsl(241, 100%, 89%);
}

.summary-sec h3 {
  color: hsl(224, 30%, 27%);
}
.summary-sec article:nth-of-type(1) {
  background-color: hsla(0, 100%, 67%, 0.104);
}
.summary-sec article:nth-of-type(1) h4:nth-child(1) {
  color: hsl(0, 100%, 67%);
}
.summary-sec article:nth-of-type(2) {
  background-color: hsla(39, 100%, 56%, 0.104);
}
.summary-sec article:nth-of-type(2) h4:nth-child(1) {
  color: hsl(39, 100%, 56%);
}
.summary-sec article:nth-of-type(3) {
  background-color: hsla(166, 100%, 37%, 0.104);
}
.summary-sec article:nth-of-type(3) h4:nth-child(1) {
  color: hsl(166, 100%, 37%);
}
.summary-sec article:nth-of-type(4) {
  background-color: hsla(234, 85%, 45%, 0.104);
  margin-bottom: 2rem;
}
.summary-sec article:nth-of-type(4) h4:nth-child(1) {
  color: hsl(234, 85%, 45%);
}
.summary-sec h4:nth-child(2) {
  color: hsl(241, 100%, 89%);
}
.summary-sec strong {
  color: hsl(224, 30%, 27%);
}

button {
  background-color: hsl(224, 30%, 27%);
  color: hsl(0, 0%, 100%);
  transition: all 0.5s ease;
}

button:hover {
  background: linear-gradient(hsl(252, 100%, 67%), hsl(241, 81%, 54%));
  cursor: pointer;
}

.attribution a {
  color: hsl(228, 45%, 44%);
}